import { ensureElement } from '../../utils/utils';
import { Component } from './Component';
export class Card extends Component {
    constructor(container, onClick) {
        super(container);
        this.button =
            container.querySelector('button') ||
                container;
        this._title = ensureElement('.card__title', container);
        this._price = ensureElement('.card__price', container);
        this.button.addEventListener('click', onClick);
    }
    set title(value) {
        this.setText(this._title, value);
    }
    set price(value) {
        if (value)
            this.setText(this._price, `${value.toLocaleString('ru-RU')} синапсов`);
        else
            this.setText(this._price, 'Бесценно');
    }
}
export class MainPageCard extends Card {
    constructor(container, onClick) {
        super(container, onClick);
        this._image = ensureElement('.card__image', container);
        this._category = ensureElement('.card__category', container);
    }
    set image(value) {
        this.setImage(this._image, value);
    }
    set category(value) {
        this.setText(this._category, value);
        let className = '';
        switch (value) {
            case 'софт-скил':
                className = '_soft';
                break;
            case 'хард':
                className = '_hard';
                break;
            case 'другое':
                className = '_other';
                break;
            case 'дополнительное':
                className = '_additional';
                break;
            case 'кнопка':
                className = '_button';
                break;
        }
        this._category.classList.add('card__category' + className);
    }
}
export class PreviewCard extends Card {
    constructor(container, onClick) {
        super(container, onClick);
        this._image = ensureElement('.card__image', container);
        this._category = ensureElement('.card__category', container);
        this._description = ensureElement('.card__text', container);
    }
    set image(value) {
        this.setImage(this._image, value);
    }
    set category(value) {
        this.setText(this._category, value);
        let className = '';
        switch (value) {
            case 'софт-скил':
                className = '_soft';
                break;
            case 'хард':
                className = '_hard';
                break;
            case 'другое':
                className = '_other';
                break;
            case 'дополнительное':
                className = '_additional';
                break;
            case 'кнопка':
                className = '_button';
                break;
        }
        this._category.classList.add('card__category' + className);
    }
    set description(value) {
        this.setText(this._description, value);
    }
    set isPurchased(state) {
        if (state) {
            this.button.classList.add('button_alt');
            this.setText(this.button, 'Убрать из корзины');
        }
        else {
            this.button.classList.remove('button_alt');
            this.setText(this.button, 'В корзину');
        }
    }
}
export class BasketCard extends Card {
    constructor(index, container, onClick) {
        super(container, onClick);
        this.setText(ensureElement('.basket__item-index', container), index);
    }
}
