import { ensureElement } from "../../utils/utils";
import { Component } from "./Component";
export class Basket extends Component {
    constructor(container, events) {
        super(container);
        this._basketContainer = ensureElement('.basket__list', container);
        this.button = ensureElement('.button', container);
        this._price = ensureElement('.basket__price', container);
        this.button.addEventListener('click', () => {
            events.emit('basket:submit');
        });
    }
    set basketContainer(items) {
        if (items.length === 0) {
            this.setDisabled(this.button, true);
        }
        else {
            this.setDisabled(this.button, false);
        }
        this._basketContainer.replaceChildren(...items);
    }
    set price(value) {
        this.setText(this._price, `${value.toLocaleString('ru-RU')} синапсов`);
    }
}
