import { ensureElement } from "../../utils/utils";
import { Component } from "./Component";
export class Page extends Component {
    constructor(page, events) {
        super(page);
        this.basket = ensureElement('.header__basket', page);
        this._counter = ensureElement('.header__basket-counter', this.basket);
        this._catalog = ensureElement('.gallery', page);
        this.wrapper = ensureElement('.page__wrapper', page);
        this.basket.addEventListener('click', () => {
            events.emit('basket:open');
        });
    }
    set counter(value) {
        this.setText(this._counter, value);
    }
    set catalog(items) {
        this._catalog.replaceChildren(...items);
    }
    set locked(value) {
        this.wrapper.classList.toggle('page__wrapper_locked', value);
    }
}
